.WhyUsBox {
  display: grid;
  grid-template-columns: 0.4fr 0.6fr;
}

.whyus {
  padding: 9rem;
  position: relative;
}

.whyUsTri {
  width: 0;
  height: 0;
  border-top: 37px solid white;
  border-right: 42px solid transparent;
  position: relative;
  left: 60rem;
  margin-top: -3rem;
  transform: rotate(90deg);
}

.textBox {
  background-color: white;
  border-radius: 6px;
  font-weight: 500;
  color: #9e9ea0;
}

.rightArrow {
  position: relative;
  left: 66rem;
  height: 3vh;
}

.leftArrow {
  position: relative;
  left: 32rem;
  height: 3vh;
}
.textBox {
  background-color: white;
  border-radius: 6px;
}
.textBox__para {
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 30px;
  color: #848589;
}
.textBox__name {
  padding: 20px 0 0 0;
  font-family: "Poppins";
  font-weight: 600;
  font-size: 20px;
  color: #2d2d2d;
}
.textBox__designation {
  padding: 10px 0 0 0;
  font-weight: 500;
  color: #9e9ea0;
}
.OurServiceBox {
  background: linear-gradient(
    90deg,
    rgba(0, 0, 0, 0.88) 0%,
    rgba(5, 91, 87, 0.88) 125.98%,
    rgba(8, 140, 135, 0.88) 195.07%
  );
}

.ServiceContent {
  display: grid;
  grid-template-columns: 0.55fr 0.45fr;
  gap: 20px;
}

.ServiceContent svg {
  transform: scale(0.5);
}

.BigBoxLayout {
  max-width: 260px !important;
  min-height: 298px;
  border-radius: 8px !important;
  padding: 16px !important;
}

.cardwrapper {
  position: relative;
}

.BigBoxLayout svg {
  justify-content: flex-start;
  display: flex;
}

.BigBoxLayout h3 {
  color: #003d3a;
}

.BigBoxLayout p {
  color: #848589;
}

.hoverAnimation2 {
  position: absolute;
  left: 25.8rem;
}

.hoverAnimation {
  position: absolute;
  left: 17.8rem;
}

.hoverAnimation3 {
  position: absolute;
  left: 44.2rem;
}

.hoverAnimation4 {
  position: absolute;
  left: 44.2rem;
}

.hoverAnimation4 img {
  height: 30vh !important;
  margin-left: -2rem;
}

.AlignBoxes {
  padding: 40px 0;
  display: flex;
  flex-direction: column;
  row-gap: 30px;
}

.AlignBoxesTwo {
  padding-top: 120px !important;
  display: flex;
  flex-direction: column;
  row-gap: 30px;
}

.iconLayout {
  width: 50px;
  padding-bottom: 0px !important;
}

.ourServiceRight {
  padding: 192px 0 0 30px;
}

.ourServiceRight--mainText {
  text-transform: uppercase;
  color: var(--primary);
  letter-spacing: 0.08em;
}

.ourServiceRight--heading {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 700;
  color: #ffffff;
  line-height: 42px;
  padding-right: 20px;
}

.ourServiceRight--text {
  line-height: 38px;
  color: rgba(255, 255, 255, 0.7);
  font-weight: 500;
}

@media screen and (max-width: 900px) {
  .ServiceContent {
    display: flex;
    flex-direction: column;
  }

  .ourServiceRight--mainText {
    text-transform: uppercase;
    font-size: 14px;
    letter-spacing: 0.08em;
  }

  .ourServiceRight {
    order: 0;
    padding: 0;
    text-align: center;
  }

  .leftContent {
    order: 1;
  }
}
