@import url("https://fonts.googleapis.com/css2?family=Inter&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Inter&family=Poppins&display=swap");

.aboutUs {
  display: flex;
  margin-top: 8rem;
}

.aboutUsLeft {
  position: relative;
  align-items: center;
  flex: 1;
  box-sizing: border-box;
}

.leftStartrectangle {
  height: 188.47px;
  width: 295.86px;
}

.leftIconText {
  width: 80%;
  padding-top: 2rem;
  margin: auto;
}

.leftIconText > img {
  transform: scale(0.5);
  position: absolute;
  fill: #ffff !important;
  top: -2rem;
  left: 7rem;
}

.leftIconText > span {
  color: #ffff;
  font-family: Inter;
  font-weight: 500;
  font-size: 12px;
  line-height: 36px;
  font-style: italic;
}

.lefStart {
  position: absolute;
  display: flex;
  right: 0rem;
  top: 27rem;
}

.polygon {
  width: 0;
  height: 0;
  border-top: 37px solid var(--primary);
  border-right: 42px solid transparent;
  /* background-color: var(--primary); */
  position: absolute;
  top: 9.4rem;
  left: -2.4rem;
  z-index: -4;
  transform: rotate(88deg);
}

.aboutUsLeft > img {
  height: 35rem;
  width: 100%;
  box-sizing: border-box;
}

.aboutUsRight {
  text-align: left;
  flex: 1;
  margin-left: 5rem;
}

.aboutUsRight > p {
  margin-top: 2rem;
  font-family: "Inter";
  font-style: normal;
  font-weight: 500;
  font-size: 15px;
  color: #9d9797;
  line-height: 36px;
  text-align: left;
}

.aboutUsRight > h1 {
  margin-top: 2rem;
  color: #464646;
  padding-right: 5rem;
  line-height: 3rem;
  font-size: 26px;
}

.aboutUsRight > img {
  transform: scale(0.8);
  margin-top: 1rem;
  margin-left: -1rem;
  margin-bottom: -1rem;
}

.aboutUsSpan {
  color: var(--primary);
  font-family: Inter;
  font-size: 1rem;
  margin-bottom: 3rem;
}

.rightIcon {
  display: flex;
}

.icon-back {
  width: 3.5rem;
  margin-right: 1rem;
  height: 3.5rem;
  border-radius: 100%;
  background-color: #ffff;
  box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.3);
}

.rightIconBox {
  margin-top: 2rem;
}

.rightIcon img {
  transform: scale(0.5);
}

.rightIcon h6 {
  font-family: Poppins;
  font-weight: 600;
  font-size: 1rem;
  color: #000000;
}

.rightIcon p {
  font-family: Inter;
  font-weight: 500;
  font-size: 15px;
  line-height: 1.5rem;
  color: #9d9797;
  padding-right: 1rem;
}

@media screen and (max-width: 900px) {
  .aboutUs {
    flex-direction: column;
  }

  .aboutUsLeft > img {
    object-fit: cover;
  }

  .aboutUsRight {
    margin-left: 0;
    margin-top: 4rem;
  }

  .aboutUsSpan {
    font-weight: bold;
  }

  .leftIconText {
    padding-top: 1rem;
    margin: auto;
  }

  .leftIconText > span {
    font-size: 12px;
    line-height: 20px;
    font-style: italic;
  }

  .leftStartrectangle {
    height: 130px;
    width: 220px;
    margin-top: 5rem;
  }

  .leftIconText > img {
    width: 64px;
    top: 3rem;
    left: 5rem;
  }

  .polygon {
    width: 0;
    height: 0;
    border-top: 37px solid var(--primary);
    border-right: 42px solid transparent;
    position: absolute;
    top: 172.2px;
    left: -38px;
    z-index: 4;
  }
}
