.footerBox {
  display: flex;
  position: relative;
}

.footerContainer {
  background-color: #091823;
  padding-top: 3rem;
}

.footerContainerMobile {
  background-color: #091823;
  position: relative;
  height: 466px;
  top: 551px;
}

.contactForm {
  background-color: #ffffff;
  border-top: 2px solid #43aea7;
  width: 100%;
  max-width: 400px;
  transition: background 0.3s, border 0.3s, border-radius 0.3s, box-shadow 0.3s;
  position: relative;
  top: -100px;
  margin-left: 30px;
}

.contactFormMobile {
  background-color: #ffffff;
  border-top: 2px solid #43aea7;
  width: 94%;
  transition: background 0.3s, border 0.3s, border-radius 0.3s, box-shadow 0.3s;
  position: relative;
  top: -658px;
  margin-left: 10px;
}

.contactTitleMobile {
  text-align: center;
}

.contactTitleMobile h2 {
  color: #091823;
  font-size: 20px;
  font-weight: 500;
}

.contactTitle {
  text-align: center;
  padding: 2rem;
}

.contactTitle h2 {
  color: #091823;
  font-size: 30px;
  font-weight: 500;
}
.input1 {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex: 1;
}

.input1 input {
  width: calc(100% - 80px);
  margin: 10px 20px;
  background-color: #ffffff;
  border: 1px solid #818a91;
  background-color: transparent;
  color: #373a3c;
  flex-grow: 1;
  height: 35px;
}

.input1 textarea {
  background-color: #ffffff;
  width: calc(100% - 80px);
  border: 1px solid #818a91;
  background-color: transparent;
  color: #373a3c;
  min-height: 85px;
  max-height: 125px;
  margin: 10px 20px 20px;
  max-width: calc(342px - 20px);
  flex-grow: 1;
}

.input1Mobile {
  margin-bottom: 10px;
  width: 100%;
  flex-wrap: wrap;
  align-items: center;
  left: -19px;
  position: relative;
}

.input1Mobile input {
  flex-basis: 100%;
  background-color: #ffffff;
  max-width: 100%;
  width: 100%;
  max-width: 100%;
  border: 1px solid #818a91;
  background-color: transparent;
  color: #373a3c;
  vertical-align: middle;
  flex-grow: 1;
  width: 80%;
  height: 35px;
  margin-left: 40px;
  padding-bottom: -13px;
  margin-bottom: 10px;
}

.input1Mobile textarea {
  flex-basis: 100%;
  background-color: #ffffff;
  max-width: 100%;
  width: 100%;
  max-width: 100%;
  border: 1px solid #818a91;
  background-color: transparent;
  color: #373a3c;
  vertical-align: middle;
  flex-grow: 1;
  width: 80%;
  height: 85px;
  margin-left: 40px;
  padding-bottom: -13px;
  margin-bottom: 10px;
}

input[type="email"],
input[type="text"] {
  padding: 1px 10px;
}

textarea {
  padding: 5px 10px;
}

.submitBtn {
  padding-right: calc(10px / 2);
  padding-left: calc(10px / 2);
  margin-bottom: 10px;
  padding-bottom: 20px;
  text-align: center;
}

.submitBtnMobile {
  padding-right: calc(10px / 2);
  padding-left: calc(10px / 2);
  margin-bottom: 10px;
  padding-bottom: 40px;
}

.submitBtnMobile button {
  background-color: #43aea7;
  color: #ffffff;
  font-size: 15px;
  padding: 14px 40px;
  border-radius: 50px 50px 50px 50px;
  width: 83%;
  margin-left: 21px;
  margin-top: 18px;
}

.submitBtn button {
  font-size: 18px;
}

.submitBtn h6 {
  font-size: 15px;
  font-weight: 500;
  padding-top: 30px;
}

.icon-box1 {
  display: flex;
  column-gap: 20px;
  align-items: center;
}

.linkedin {
  background-color: white;
  border-radius: 34px;
  height: 56px;
  width: 56px;
  text-align: center;
  display: flex;
  justify-content: center;
}

.linkedin > a {
  text-decoration: none;
}

.messageIcon {
  background-color: transparent;
  border: 3px solid white;
  border-radius: 34px;
  height: 50px;
  width: 50px;
  text-align: center;
}

.messageIcon i {
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  font-size: 25px;
  position: relative;
  top: 12px;
  color: white;
}
