.header-logo {
  height: 2rem;
  border-radius: 2rem;
}

.headerul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: inline-flex;
}

.headerul > li {
  padding: 1rem;
  color: #413f42;
  font-size: 1rem;
  cursor: pointer;
}
.headerul > li > a {
  padding: 1rem;
  color: #413f42;
  font-size: 1rem;
  cursor: pointer;
  text-decoration: none;
}

.headerul > li > a:hover {
  color: #fff;
  background-color: #413f42;
}

.headerul > li:hover {
  color: #fff;
  background-color: #413f42;
}

.header-container {
  background-color: rgb(250, 194, 19);
  position: fixed;
  width: 100%;
  margin-top: 3rem;
  z-index: 20;
}
.header-container-top {
  background-color: rgb(250, 194, 19);
  position: fixed;
  width: 100%;
  z-index: 20;
}

.icon-box {
  padding: 1rem;
}

.icon-box-container {
  z-index: 2;
  right: 0rem;
  background-color: #cabcb996;
  width: 100%;
  height: 3rem;
  position: fixed;
}

.box-b {
  display: flex;
  position: absolute;
  right: 0rem;
}
